* {
    margin: 0;
    padding: 0;
    box-sizing: content-box;
}
#root {
    min-width: 320px;
    max-width: 1440px;
    margin: 0 auto;
}
.App {
    margin-bottom: 40px;
}
.main {
    background-color: #585CC6;
    flex: 1 0 auto;
}
.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.review {
    padding: 56px;
    background-color: white;
}

html,body {
    font-family: 'Gilroy';
    font-size: 14px;
    font-family: normal;
    height: 100%;
}

@font-face {
    font-family: 'Factor A';
    src: local('Factor A Medium'), local('Factor-A-Medium'),
        url('../fonts/FactorA/FactorAMedium-Regular.woff2') format('woff2'),
        url('../fonts/FactorA/FactorAMedium-Regular.woff') format('woff'),
        url('../fonts/FactorA/FactorAMedium-Regular.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Gilroy';
    src: url('../fonts/GilroyRegular/Gilroy-Regular.eot');
    src: url('../fonts/GilroyRegular/Gilroy-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/GilroyRegular/Gilroy-Regular.woff') format('woff'),
        url('../fonts/GilroyRegular/Gilroy-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Gilroy';
    src: url('../fonts/GilroySemibold/Gilroy-SemiBold.eot');
    src: url('../fonts/GilroySemibold/Gilroy-SemiBold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/GilroySemibold/Gilroy-SemiBold.woff') format('woff'),
        url('../fonts/GilroySemibold/Gilroy-SemiBold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}
